import React from 'react'; 
import emailjs from 'emailjs-com';

class Form extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        name: "",
        email: "",
        message: "",
        website: "",
        buttonText: "Pošalji prijavu",
      };
      } 
      sendEmail = (e) => {
        e.preventDefault();
        emailjs
          .sendForm(
            "service_bc8hvy9",
            "template_577qnnn",
            e.target,
            "user_GBh99Ol9hSr9OWn0gnEpZ"
          )
          .then(
            (result) => {
              console.log(result.text);
            },
            (error) => {
              console.log(error.text);
            }
          );
        this.resetForm();
      };
      resetForm() {
        this.setState({
          name: "",
          email: "",
          message: "",
          website: "",
          buttonText: "Poslano!",
        });
      }
      handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
      };
     
    render() {
      const { name, email, message, website, buttonText } = this.state;
      return (
        <form onSubmit={this.sendEmail.bind(this)} className="form">
            <div id="ucestvuj">Želite učestvovati?</div>
            <input
              type="text"
              value={name}
              placeholder="Ime i prezime"
              name="name"
              autoComplete="off"
              id="name"
              onChange={this.handleChange}
            />
            <input
              type="text"
              value={email}
              placeholder="Email"
              autoComplete="off"
              name="email"
              id="email"
              onChange={this.handleChange}
            />
            <input
              type='text'
              placeholder='Vaš hobi'
              value={message}
              autoComplete="off"
              name="message"
              id="message"
              onChange={this.handleChange}
            /> 
            <input
              type='text'
              placeholder='Website / link na vaš rad (opcionalno)'
              value={website}
              name="website"
              autoComplete="off"
              id="website"
              onChange={this.handleChange}
            />             
            <input type="submit" value={buttonText}></input>
        </form>
      )
    }
  }
  

export default Form;

